<template>
    <div class="bg-grey-200" style="height:100vh;">
  
        <div style="height:100vh;" class="d-flex justify-content-center align-items-center shadow-lg rounded-lg">
            <div style="width:100%">
                <div class="p-5">
                    <img  style="display:block;margin:auto;width:40vw;"
                    src="https://www.pthis.id/public/img/hasnur/logo.png" alt="">
                    <p class="text-center text-xl p-2 font-semibold">Selamat Datang!</p>
                    <p class="text-lg font-semibold text-center text-blue-800">Masuk untuk melanjutkan</p>
                    <br>
                    <br>
                    <form action="" @submit.prevent="login">
                        <div class="sm-form ">
                            <input type="text" required id="nrp" name="nrp" class="form-control p-2  rounded-lg shadow" placeholder="nrp" v-model="vdata['nrp']" >
                        </div>
                        <div class="sm-form mt-2">
                            <input type="password" required id="password" name="password" class="form-control p-2  rounded-lg shadow" placeholder="Kata sandi" v-model="vdata['password']" >
                        </div>
                        <div class="text-center">
                            <button type="submit" class=" mt-2 btn btn-sm btn-style9 rounded-lg font-semibold btn-block">Masuk</button>
                        </div>
                    </form>
                </div>        
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import autophp from '@/plugins/auto';
let sdb = new autophp('ci');
var watchId;
export default{
    data(){
        return{
            vdata:{}
        }
    },
    methods: {
        login(){
            function appendLocation(location, verb) {
               
            }
            if ("geolocation" in navigator) {
                navigator.geolocation.getCurrentPosition(function (location) {
                    appendLocation(location, "fetched");
                });
                watchId = navigator.geolocation.watchPosition(appendLocation);
            } else {
                sdb.alert("Geolocation API not supported.");
            }
            let data={
                nrp:this.vdata.nrp,
                password:this.vdata.password
            }
            let fd=new FormData();
            fd.append('nrp',this.vdata.nrp);
            fd.append('password',this.vdata.password);
            axios.post('https://officialapp.my.id/api/bridge/sign_in',fd).then(res=>{
                if(res.data.message){
                    if(res.data.message=="INVALID_PASSWORD" || res.data.message=="USER_NOT_FOUND"){
                        sdb.alert('Login Gagal Username dan password salah!');
                    }else if(res.data.message="Success"){
                        localStorage.setItem('users',sdb.scramble(JSON.stringify(res.data.data)));
                        this.$router.push('/main')
                    }
                }
            });
        }
    },
    mounted() {
         if( /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
            // some code..
            }else{
            this.$router.push('/404')
        }
    },
}
</script>